import React from "react";
import ReactDOM from "react-dom";
import { App } from "../components/App";
import state from "../state";

// Header render dependencies
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

// Rendering fully body content
export const Render = () => {
  ReactDOM.render(
    <React.StrictMode>
      <App {...state} />
    </React.StrictMode>,
    document.getElementById("root")
  );
};

// Rendering only one piece of body content
export const HeaderRender = (props) => {
  return (
    <header className={props.header.header}>
      <div className={props.header["header-container"]}>
        <p className={props.header["header-title"]}>
          <a href="/">Dooboox</a>
        </p>
        <div
          className={
            props.header["header-searcher"] +
            " " +
            props.header[props.searcherActive]
          }
          ref={props.searcher}
        >
          <input
            type="text"
            name="searcher"
            id={props.header.search}
            value={props.state.searchExpression}
            ref={props.searchLine}
            onChange={(e) => props.search(e.target)}
          />
          <div
            className={props.header["search-icon_box"]}
            onClick={() => props.toggle()}
          >
            <FontAwesomeIcon
              icon={faSearch}
              className={props.header["search-icon"]}
            />
          </div>
        </div>
      </div>
    </header>
  );
};
