import React, { Component } from "react";
import { HeaderRender, Render } from "../js/Render";

// CSS modules
import header from "../css/modules/Header.module.css";

// Header UI functions and rendering
export class Header extends Component {
  constructor(props) {
    super();

    // State
    this.state = {};
    this.state.dispatch = props.mainPage.dispatch;
    this.state.updateValue = props.mainPage.updateValue;
    this.state.search = props.mainPage.search;

    this.state.searchExpression = "";

    // Screen width contorl
    this.screenWidth = document.body.clientWidth;
    this.width = props.mainPage.width;

    // Search line element
    this.searcher = React.createRef(); // Get searcher
    this.searchLine = React.createRef(); // Get search input element
  }

  // Toggle mode of search line
  toggle = () => {
    // Only for mobile sceen widths
    if (this.screenWidth <= this.width) {
      // Add toggle class fof searcher
      if (this.state.searcherActive) {
        this.setState({ searcherActive: false });
      } else {
        this.setState({ searcherActive: "searcher_active" });
      }
      this.setState({ searchExpression: "" }); // Delete all from searcher input
    }
  };

  // Bind toggle function with this context
  toggle = this.toggle.bind(this);

  // Search entered expression in body content(texts, titles)
  search = (element) => {
    this.state.search(element);
    return this.updateValue(element);
  };

  // Update search line value (FLUX)
  updateValue = (element) => {
    // Search inputed expression in body content
    return (this.state.searchExpression = this.state.dispatch({
      action: this.state.updateValue,
      args: {
        value: element.value,
        renderProps: {
          header: header,
          state: this.state,
          searchLine: this.searchLine,
          search: this.search,
          toggle: this.toggle,
        },
      },
    }));
  };

  render() {
    return (
      <React.StrictMode>
        {HeaderRender({
          header: header,
          state: this.state,

          searchLine: this.searchLine,
          searcher: this.searcher,

          searcherActive: this.state.searcherActive,

          search: this.search,
          toggle: this.toggle,
        })}
      </React.StrictMode>
    );
  }
}
