import React, { Component } from "react";

// CSS Styles
import "../css/App.css";

// Components
import { Header } from "./Header";
import { Main } from "./Main";
import { Footer } from "./Footer";

// Component class
export class App extends Component {
  constructor(props) {
    super();
    this._mainPage = props.mainPage;
  }

  render() {
    return (
      <React.StrictMode>
        <Header mainPage={this._mainPage} />
        <Main {...this._mainPage} />
        <Footer />
      </React.StrictMode>
    );
  }
}
