import React, { Component } from "react";
import { Route } from "react-router-dom";

// Components
import { Developer } from "./Authors/Developer";
import { Redactor } from "./Authors/Redactor";
import { Writer } from "./Authors/Writer";
import { Default } from "./Authors/Default";

export class AuthorsPage extends Component {
  constructor(props) {
    super();
    this._menu = props.menu;
    this._state = props.state;
  }

  render() {
    return (
      <React.StrictMode>
        <Route
          exact
          path={`/${this._menu.authors}/id=1`}
          component={Developer}
        />
        <Route
          exact
          path={`/${this._menu.authors}/id=2`}
          component={Redactor}
        />
        <Route exact path={`/${this._menu.authors}/id=3`} component={Writer} />
        <Route exact path={`/${this._menu.authors}`} component={Default} />
      </React.StrictMode>
    );
  }
}
