import { HeaderRender, Render } from "./Render";

export class Searcher {
  constructor(options) {
    this.expression = options.value;
    this.renderProps = options.renderProps;

    this.searcher = options.searcher;

    this.state = {};
    this.state.searchItems = [
      {
        name: "title",
        item: ".tp-title",
      },
      {
        name: "text",
        item: ".tp-text",
      },
    ];
  }

  search = () => {
    let string = this.expression.toLowerCase().trim();
    let items = this.state.searchItems.map((i) => {
      return document.querySelectorAll(i.item);
    });

    if (string != "") {
      items.forEach((node) => {
        node.forEach((i) => {
          if (i.innerText.toLowerCase().search(string) !== -1) {
            i.classList.remove("_hidden");
            let markedString = this._marker(this.expression.trim());
            i.innerHTML = i.innerText
              .toLowerCase()
              .replace(string, markedString);
          } else {
            i.classList.add("_hidden");
          }
        });
      });
    } else {
      items.forEach((node) => {
        node.forEach((i) => {
          i.classList.remove("_hidden");
          i.innerHTML = i.innerHTML
            .replace("<mark>", "")
            .replace("</mark>", "");
        });
      });
    }
  };

  _marker = (element) => {
    return `<mark>${element}</mark>`;
  };

  updateValue = (props) => {
    Render();
    HeaderRender(props);
    return this.expression;
  };
}
