import React, { Component } from "react";

// Utils
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLink } from "@fortawesome/free-solid-svg-icons";
import { NavLink } from "react-router-dom";

export class LinksBlock extends Component {
  // Use to add block of some links under a title or smth like it.
  constructor(props) {
    super();
    this.state = {};
    this.state.links = props.links;
  }

  link = (props) => {
    // Generate a link
    if (props.refresh) {
      return (
        <a href={props.link} key={props.key} className="tp-link">
          <FontAwesomeIcon icon={faLink} />
          {props.value}
        </a>
      );
    } else {
      return (
        <NavLink to={props.link} key={props.key} className="tp-link">
          <FontAwesomeIcon icon={faLink} />
          {props.value}
        </NavLink>
      );
    }
  };

  linksBlock(props) {
    // Generate a links block
    let links = props.map((item) => {
      return this.link({
        key: item.id,
        value: item.value,
        link: item.link,
        refresh: item.refresh,
      });
    });

    return <div className="tp-links_block">{links}</div>;
  }

  render() {
    return (
      <React.StrictMode>{this.linksBlock(this.state.links)}</React.StrictMode>
    );
  }
}
