import React, { Component } from "react";

// Components
import { FullsizeMenu } from "./FullsizeMenu";
import { CompactMenu } from "./CompactMenu";

export class Menu extends Component {
  constructor(props) {
    super();
    this.menu = props.menu;
    this.state = {};
    this.state.props = props;
    this.state.screenWidth = document.body.clientWidth;
  }

  render() {
    return (
      <React.StrictMode>
        {this.state.props.width >= this.state.screenWidth ? (
          <CompactMenu menu={this.menu} />
        ) : (
          <FullsizeMenu menu={this.menu} />
        )}
      </React.StrictMode>
    );
  }
}
