import React, { Component } from "react";
import { BrowserRouter, HashRouter, Route } from "react-router-dom";

// CSS Styles
import "../css/Main.css";

// Components
import { Menu } from "./Menu";

import { HomePage } from "./Pages/HomePage";
import { ContentPage } from "./Pages/ContentPage";
import { AuthorsPage } from "./Pages/AuthorsPage";
import { AboutPage } from "./Pages/AboutPage";

export class Main extends Component {
  // Main block of application
  constructor(props) {
    super(); // Compontent consturctor
    this._props = props;
    this._menu = props.menu;
    this._images = props.images;
  }

  render() {
    return (
      <BrowserRouter>
        <React.StrictMode>
          <main>
            <Menu {...this._props} />
            <Route
              exact
              path={`/${this._menu.home}`}
              render={(props) => (
                <HomePage {...props} menu={this._menu} images={this._images} />
              )}
            />
            <Route
              exact
              path={`/${this._menu.content}`}
              render={(props) => (
                <ContentPage {...props} images={this._images} />
              )}
            />
            <Route
              exact
              path={`/${this._menu.authors}*`}
              render={(props) => <AuthorsPage {...props} menu={this._menu} />}
            />
            <Route
              exact
              path={`/${this._menu.about}`}
              render={(props) => <AboutPage {...props} images={this._images} />}
            />
          </main>
        </React.StrictMode>
      </BrowserRouter>
    );
  }
}
