import React, { Component } from "react";

export class SublinksBlock extends Component {
  // Use to add a list of some subtitle under an image or smth like it
  constructor(props) {
    super();
    this.state = {};
    this.state.sublinks = props.sublinks;
  }

  sublink = (props) => {
    // Generate a sublink
    return (
      <a href={props.link} key={props.key} className="tp-sublink">
        {props.value}
      </a>
    );
  };

  sublinksBlock = (props) => {
    // Generate a sublinks block
    let sublinks = props.map((item) => {
      return this.sublink({
        key: item.id,
        value: item.value,
        link: item.link,
      });
    });

    return <div className="tp-sublinks_block">{sublinks}</div>;
  };

  render() {
    return this.sublinksBlock(this.state.sublinks);
  }
}
