import React, { Component } from "react";
import { NavLink } from "react-router-dom";

// CSS Styles
import "../css/CompactMenu.css";
import { Render } from "../js/Render";

export class CompactMenu extends Component {
  constructor(props) {
    // Props from Menu
    super();
    this.state = {};
    this.state.menu = props.menu;
    this.state.curtainActive = false;

    // Toggle effect elements
    this.curtain = React.createRef();
    this.cross = React.createRef();
  }

  toggle = () => {
    // Just dunno how it is working.
    // You mustn't recognize it or you'll shit your pants =)
    if (!this.state.curtainActive) {
      this.state.curtainActive = "curtain_active";
      // Lock body scroll (overflow-hidden)
      document.body.classList.add("scroll-lock");
    } else {
      this.state.curtainActive = false;
      // Unlock body scroll (overflow-hidden)
      document.body.classList.remove("scroll-lock");
    }

    Render();

    // Why not?
    return true;
  };

  render() {
    return (
      <React.StrictMode>
        <menu>
          <div className="compact-menu" onClick={() => this.toggle()}>
            <p className="compact-menu_title">МЕНЮ</p>
            <div
              className={"compact-menu_curtain " + this.state.curtainActive}
              ref={this.curtain}
            >
              <div className="compact-menu_cross" title="Закрыть"></div>
              <menu className="compact-menu_list">
                <li className="compact-menu_item">
                  <NavLink
                    to={`/${this.state.menu.home}`}
                    className="fas fa-home compact-menu_link"
                  >
                    ГЛАВНАЯ
                  </NavLink>
                </li>
                <li className="compact-menu_item">
                  <NavLink
                    to={`/${this.state.menu.content}`}
                    className="fas fa-align-left compact-menu_link"
                  >
                    СОДЕРЖАНИЕ
                  </NavLink>
                </li>
                <li className="compact-menu_item">
                  <NavLink
                    to={`/${this.state.menu.authors}`}
                    className="fas fa-user-astronaut compact-menu_link"
                  >
                    АВТОРЫ
                  </NavLink>
                </li>
                <li className="compact-menu_item">
                  <NavLink
                    to={`/${this.state.menu.about}`}
                    className="fas fa-project-diagram compact-menu_link"
                  >
                    О ПРОЕКТЕ
                  </NavLink>
                </li>
              </menu>
            </div>
          </div>
        </menu>
      </React.StrictMode>
    );
  }
}
